body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.Nav-bar {
  height: 100px;
  display: flex;
  border-bottom-right-radius:50px;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
  justify-content: space-between;
}
.nav--text{
  color: black;
  text-align: center;
  justify-content: center;
}
.search-input {
  margin-left: 5px;
  margin-top: 20px;
  margin-bottom: 2rem;
  padding: 5px;
  font-size: 1rem;
}
.employee-list {
  list-style-type: none;
  padding: 0;
  margin-left: 5px;
}

.employee-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 2rem;
  font-size: 25px;
  font-weight: bold;
}

.employee-image {
  height: 300px;
  width: 300px;
  padding: 15px;
  margin-right: 0.5rem;
  border: 2px solid black;
  border-radius: 4%;
}
.employee--id{
  color: #ffff;
  text-align: center;
  font-size: 19px;
  height: 30px;
  width: 30px;
  background-color: black;
  border-radius: 20px;
  position: relative;
  top: -161px;
  right: 123px;
}
